@import url('https://fonts.googleapis.com/css2?family=Major+Mono+Display&family=Poppins&display=swap');
.poppins-regular {
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    font-style: normal;
}
.major-mono-display-regular {
    font-family: "Major Mono Display", monospace;
    font-weight: 400;
    font-style: normal;
}
*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Poppins";
}
#root{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
}
body{
    background-color: #161616;
}
h1{
    font-size: 30px ;
    font-family: "Major Mono Display";
    color: #ffffff;
    text-shadow: 0px 0px 5px ;
}
h2{
    font-size: 18px;
    margin: 15px 0;
    color: cyan;
    text-shadow: 0px 0px 10px rgb(0, 255, 255,0.65);
}
.card-container{
    width: max-content;
    text-align: center;
    background: linear-gradient(135deg,rgba(255,255,255,0.1),rgba(255,255,255,0));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid rgba(255,255,255,0.18);
    padding: 40px;
    border-radius: 25px;
    text-shadow: 0px 0px 3px;
    box-shadow: 0px 0px 32px 0 rgba(0,0,0,0.37);
}
.img{
    height: 20px;
    filter:drop-shadow(0px 0px 3px rgb(255, 255, 255,0.2));
}
.input-container{
    display: flex;
    justify-content: center;
    width: 100%;
    background: linear-gradient(135deg,rgba(255,255,255,0.1),rgba(255,255,255,0));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid rgba(255,255,255,0.18);
    box-shadow: 0px 0px 20px 0 rgba(0,0,0,0.37);
    border-radius: 25px;
    padding: 5px 15px;
    margin: 20px 0;
    cursor: pointer;
}
input[type=text]{
    flex:1;
    background-color: transparent;
    border: 0;
    outline: none;
    color: white;
    text-shadow: 0px 0px 5px rgb(255, 255, 255);
    cursor: pointer;
    caret-color: cyan;
}
input::placeholder{
    color: whitesmoke;
}
.coord{
    display: flex;
    align-items: center;
    justify-content: center;
    gap:25px;
}
.vl {
    border-left: 3px solid rgba(170, 170, 170, 0.35);
    height: 40px;
}
.type,.temp,.location,.coord{
    margin-top: 10px;
    color: whitesmoke;
    text-shadow: 0px 0px 4px rgb(245, 245, 245,0.6);
}
.type{
    margin-top: 0px;
}
.longitude,.latitude{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.weatherIcon{
    height:145px;
    filter: drop-shadow(0px 0px 15px rgba(138, 138, 138, 0.6));
}
.type,.temp,.location{
    text-transform: uppercase;
    text-shadow: 0px 0px 2px;
    font-weight: bold;
    text-shadow: 0px 0px 4px rgb(245, 245, 245,0.5);
}
.num{
    text-shadow: 0px 0px 5px rgb(245, 245, 245,0.5);
    font-weight: bold;
}
.data{
    display: flex;
    align-items: center;
    justify-content: center;
    width:100%;
    flex-direction: row;
    gap:90px;
}
.humiddata,.winddata{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color:whitesmoke;
}
.humiddata{
    margin-left: 0;
}
.dataimg{
    margin-top: 20px;
    height: 40px;
    filter: drop-shadow(0px 0px 3px rgb(245, 245, 245,0.5));
}